<template>
  <div class="default-view-organism lc-custom-scroll">
    <draggable v-model="apiEditables">
      <EditableListItemMolecule v-for="editable in apiEditables" :key="editable.id" :editable="editable"/>
    </draggable>
  </div>
</template>

<script>
import EditableListItemMolecule from './EditableListItemMolecule/EditableListItemMolecule';
import DefaultViewOrganism from './DefaultViewOrganism.hook'
import draggable from 'vuedraggable';

const hook = new DefaultViewOrganism();

export default {
  name: 'EditableListOrganism',
  beforeCreate() { hook.hook(this)},
  components:{
    EditableListItemMolecule,
    draggable
  },
  data() {
      return { 
        
      }
  },
  computed: {
    apiEditables : {
      get(){
        return this.openedDocument?.editables;
      },
      set(apiArray){
        for (const item of apiArray){
          const index = this.openedDocument?.editables.findIndex(editable => editable.id == item.id);
          this.openedDocument?.editables.splice(index,1);
        }

        for (let i=(apiArray.length-1);i >=0;i--){
          const item = apiArray[i];
          this.openedDocument?.editables.unshift(item);
        }

      }
      
    },
    ...hook.getters("openedDocument")
  }
}

</script>

<style scoped lang="scss">

.default-view-organism {
  height:100%;
  overflow-y:auto;
}

</style>
